body{
margin:0;
background: #173d58; 

}
::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background: #353c56;
  }
  ::-webkit-scrollbar-thumb {
    background: #755cd3;
    border-radius: 50%;
  }
  ::-webkit-scrollbar-thumb:hover {
    background: #8d82f3;
  }

  .cls-1{fill:#10aa50;}.cls-2{fill:#b8c4c2;}.cls-3{fill:#12924f;}.cls-4{fill:#21313c;}